@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  //  
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked;
  }

  //   ,    open-only-mob
  &.css-scroll-fixed.open-only-mob {
    @include media("<tab") {
      @include scroll-blocked;
    }
  }
}

body {
  min-width: $layout-page-min-width;

  @include font-normal;
  @include font-size($font-size, $line-height);

  color: $text-color;
  background: $bg-color url(../../img/empty.png) no-repeat left top fixed;

  @include media("<mob") {
    @include font-size($font-size--mob, $line-height--mob);
  }

  //.show-results & {
  //  @include media("<tab") {
  //    overflow: hidden;
  //  }
  //}
}

//    
//       
@if nth($layout-page-space, 2) != 0 {
  .layout-page-spaces {
    @include padding-horizontal(nth($layout-page-space, 2));

    @include media("<tab") {
      @include padding-horizontal(nth($layout-page-space--tab, 2));
    }

    @include media("<tab-s") {
      @include padding-horizontal(nth($layout-page-space--tab-s, 2));
    }

    @include media("<mob") {
      @include padding-horizontal(nth($layout-page-space--mob, 2));
    }
  }
}

//      
//       
.layout-page {
  max-width: $layout-page-max-width;
  box-sizing: content-box;

  @include push-auto;

  @include media('<tab') {
    box-sizing: border-box;
    max-width: $layout-page-max-width--tab;
  }
}

//     
@if nth($layout-text-space, 2) != 0 {
  .layout-text-spaces {
    @include padding-horizontal(nth($layout-text-space, 2));
  }
}

//       
.layout-text {
  max-width: $layout-text-width;
  box-sizing: content-box;

  @include push-auto;

  @include media('<tab') {
    max-width: $layout-text-width--tab;
    box-sizing: border-box;
  }
}

//      
.layout-flood {
  max-width: $layout-flood-width;
  //box-sizing: content-box;

  @include push-auto;

  @include media('<tab') {
    max-width: $layout-flood-width--tab;
  }
}

//      
.layout-wide {
  max-width: $layout-wide-width;
  //box-sizing: content-box;

  @include push-auto;

  @include media('<tab') {
    max-width: $layout-wide-width--tab;
  }
}

