/* ============ COMPONENTS ============ */
/* ==================================== */
/* swiper slider */
.swiper-pagination {
  pointer-events: none;
  line-height: 0;
}

.swiper-pagination-bullet {
  display: inline-block;
  vertical-align: top;
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 0.4rem;
  border: none;
  margin: 0.6rem 0.3rem 0.2rem;
  opacity: 1;
  pointer-events: auto;
  background: #6e56b0;
  box-shadow: 0 0 0 0 transparent;
  outline: none;
  transition: background-color 0.35s, box-shadow 0.35s;
}

.swiper-pagination-bullet:not(.swiper-pagination-bullet-active):hover {
  background-color: #6c667c;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  cursor: default;
  background-color: white;
  box-shadow: 0 0 0 1px black;
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 0;
}

.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0.6rem 0.3rem 0.2rem;
}

/* swiper slider end */
/* btn-def */
.btn-def {
  display: inline-block;
  vertical-align: top;
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 1.7rem 3.2rem;
  font-size: 1.8rem;
  line-height: 2.1rem;
  color: #fff;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 0.8rem;
  background: none;
  box-shadow: 0 4px 10px rgba(110, 86, 176, 0.25);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  z-index: 1;
}

@media (max-width: 639px) {
  .btn-def {
    padding: 1.2rem 3.2rem;
    font-size: 1.8rem;
    line-height: 2.1rem;
  }
}

.btn-def::before, .btn-def::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: inherit;
  z-index: -1;
  transition: opacity 0.35s;
}

.btn-def::before {
  background-color: #6c667c;
}

.btn-def::after {
  background: #6e56b0;
  background: -webkit-linear-gradient(-90deg, #6e56b0 0%, #5a4690 100%);
  background: linear-gradient(180deg, #6e56b0 0%, #5a4690 100%);
}

.btn-def:hover::after {
  opacity: 0;
}

.btn-def:disabled, .btn-def.disabled {
  cursor: default !important;
  color: white !important;
  border-color: #ccc !important;
  background: #ccc !important;
  box-shadow: none !important;
  pointer-events: none;
}

.btn-def:disabled::before, .btn-def:disabled::after, .btn-def.disabled::before, .btn-def.disabled::after {
  display: none;
}

input.btn-def {
  background: #6e56b0;
  background: -webkit-linear-gradient(-90deg, #6e56b0 0%, #5a4690 100%);
  background: linear-gradient(180deg, #6e56b0 0%, #5a4690 100%);
  border-radius: 0.8rem;
}

.no-touchevents input.btn-def:hover {
  background: #6c667c;
}

/* btn-def end */
/* btn-alt */
.btn-alt {
  display: inline-block;
  vertical-align: top;
  position: relative;
  margin: 0;
  padding: 1.7rem 3.2rem;
  font-size: 1.8rem;
  line-height: 2.1rem;
  color: #fff;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 0.8rem;
  background: #7a839e;
  box-shadow: 0 4px 10px rgba(110, 86, 176, 0.25);
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: background-color 0.35s;
}

@media (max-width: 639px) {
  .btn-alt {
    padding: 1.2rem 3.2rem;
    font-size: 1.8rem;
    line-height: 2.1rem;
  }
}

.no-touchevents .btn-alt:hover {
  background: #6c667c;
}

.btn-alt:disabled, .btn-alt.disabled {
  cursor: default !important;
  color: white !important;
  border-color: #ccc !important;
  background: #ccc !important;
  box-shadow: none !important;
  pointer-events: none;
}

.btn-alt:disabled::before, .btn-alt:disabled::after, .btn-alt.disabled::before, .btn-alt.disabled::after {
  display: none;
}

input.btn-alt {
  border-radius: 0.8rem;
}

/* btn-alt end */
/* btn with icon */
.btn-def [class*="icon-"],
.btn-alt [class*="icon-"] {
  display: inline-block;
  vertical-align: top;
  margin-right: 1.2rem;
  height: 2.1rem;
}

/* btn with icon end */
