.preloader {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: white;
  z-index: 9999;
  display: none;
}

@media (max-width: 1279px) {
  .show-preloader .preloader {
    display: block;
  }
}

.preloader::after {
  content: "Loading...";
  display: none;
  position: absolute;
  margin-top: 50px;
  margin-left: -80px;
  left: 50%;
  top: 50%;
  width: 160px;
  text-align: center;
  color: #333;
  font-size: 1.8rem;
  line-height: 2.4rem;
}

@media (max-width: 1279px) {
  .preloader::after {
    display: block;
  }
}

.tabs-panel .preloader {
  display: none;
  position: relative;
  left: auto;
  top: auto;
  height: auto;
  background: none;
}

@media (min-width: 1280px) {
  .show-preloader .tabs-panel .preloader {
    display: block;
  }
}

.tabs-panel .preloader::before {
  content: "";
  display: block;
  height: 0;
  padding-top: 100%;
}

@media (min-width: 1280px) {
  .tabs-panel .preloader::before {
    height: 0;
    padding-top: 56.25%;
  }
}

.preloader .spin {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 60px;
  width: 60px;
  margin: -30px 0 0 -30px;
  -webkit-animation: spin-1 3s linear infinite;
  animation: spin-1 3s linear infinite;
}

@-webkit-keyframes spin-1 {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin-1 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.preloader .spin span {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 60px;
  width: 60px;
  clip: rect(30px, 60px, 60px, 0);
  -webkit-animation: spin-2 1.5s cubic-bezier(0.77, 0, 0.175, 1) infinite;
  animation: spin-2 1.5s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}

@-webkit-keyframes spin-2 {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin-2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.preloader .spin span::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 60px;
  width: 60px;
  border: 5px solid transparent;
  border-top: 5px solid #6e56b0;
  border-radius: 50%;
  -webkit-animation: spin-3 1.5s cubic-bezier(0.77, 0, 0.175, 1) infinite;
  animation: spin-3 1.5s cubic-bezier(0.77, 0, 0.175, 1) infinite;
}

@-webkit-keyframes spin-3 {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin-3 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.preloader .spin span::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  height: 60px;
  width: 60px;
