/* dropzone */
.dropzone {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  will-change: opacity, visibility;
  transition-property: opacity, visibility;
  transition-duration: 0.05s;
  transition-timing-function: ease-in-out;
}

.dropzone.show {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}

@media (max-width: 1279px) {
  .dropzone {
    display: none;
  }
}

.dropzone::before {
  content: "";
  display: block;
  position: absolute;
  left: 10%;
  top: 10%;
  height: 80%;
  width: 80%;
  border: 2px dashed rgba(255, 255, 255, 0.8);
  border-radius: 2vw;
}

.dropzone__icon {
  display: inline-block;
  height: calc(6vw + 20px);
  width: calc(6vw + 20px);
  margin-bottom: 2vh;
  background-repeat: no-repeat;
  background-position: 50%;
  background-image: url(data:image/svg+xml;base64,);
  background-size: contain;
  opacity: 0.5;
}

.dropzone__title {
  font-size: calc(4vw + 10px);
  line-height: 1.3;
  color: white;
  opacity: 0.5;
}

/* dropzone end */
